.profile {
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 15px;
  width: 370px;
  box-shadow: 0 0 10px #c6cccced;
  border-radius: 10px;
  margin: 0 auto;
}
.description {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: inherit;
  border-bottom: #7e6f7498 solid 2px;
}
.avatar {
  width: 300px;
  box-shadow: 0 0 3px 0px #0000004d;
  border-radius: 30%;
  background-color: #00bfff;
  padding: 20px 10px 0px 10px;
  margin-bottom: 15px;
}
.name {
  font-weight: 700;
  font-size: 30px;
  margin-bottom: 15px;
  color: #524a4d;
}
.tag {
  font-weight: 500;
  font-size: 20px;
  color: #524a4d;
  margin-bottom: 15px;
}
.stats {
  display: flex;
  height: 70px;
  list-style: none;
  flex-direction: row;
  justify-content: space-around;
  margin: auto;
  padding: 0;
  width: inherit;
  background-color: #d3d3d3;
  border-bottom-right-radius: 10px;
  border-bottom-left-radius: 10px;
}
.stats li {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.location {
  font-weight: 700;
  font-size: 20px;
  color: #524a4d;
  margin-bottom: 15px;
}
.quantity {
  font-weight: 800;
  font-size: 20px;
  color: #524a4d;
}
.label {
  margin-right: 3px;
  font-weight: 700;
  font-size: 20px;
  color: #696969;
  margin-bottom: 5px;
}
